body{margin:0;font-family:"Roboto",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}button{color:white;border-radius:3px;border:none;cursor:pointer;font-family:"Roboto",sans-serif}a{text-decoration:none}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{margin:auto}.container{margin:auto;max-width:1366px;position:relative;height:100%}

#notification-panel{background-color:#e5e5e5;top:0;width:100vw;position:fixed;z-index:2;border-bottom:2px solid #9C9FA2}#notification-panel .content{max-width:720px;margin:auto;display:flex;justify-content:center;align-items:center;line-height:1.4;padding:0 5vw 5px 5vw}@media (max-width: 480px){#notification-panel .content{flex-direction:column;justify-content:baseline;align-items:baseline;font-size:11px;max-width:100%;word-wrap:break-word}}#notification-panel .content .button{width:140px;background-color:#007bc1;font-size:16px;margin-left:20px;padding:10px 0}@media (max-width: 480px){#notification-panel .content .button{margin-left:0;font-size:14px;width:55px;padding:8px 0}}@media (max-width: 480px){#notification-panel .content .info{width:90vw}}#notification-panel-not-fixed{background-color:#e5e5e5;top:0;width:100vw}#notification-panel-not-fixed .content{max-width:720px;margin:auto;display:flex;justify-content:center;align-items:center;line-height:1.4;padding:0 5vw 5px 5vw}@media (max-width: 480px){#notification-panel-not-fixed .content{flex-direction:column;justify-content:baseline;align-items:baseline;font-size:11px;max-width:100%;word-wrap:break-word}}#notification-panel-not-fixed .content .button{width:140px;background-color:#007bc1;font-size:16px;margin-left:20px;padding:10px 0}@media (max-width: 480px){#notification-panel-not-fixed .content .button{margin-left:0;font-size:14px;width:55px;padding:8px 0}}@media (max-width: 480px){#notification-panel-not-fixed .content .info{width:90vw}}

.hero{height:600px;background:#007bc1;background-image:url(/front-end-exercise/static/media/hero-background-image.a5a74b4f.jpg);background-blend-mode:multiply;background-position:0 -80px;background-attachment:fixed;background-size:cover;display:block;color:white;position:relative}@media (max-width: 480px){.hero{background-position:-400px -60px}}.hero .logo{width:55px;z-index:1;margin:0;position:absolute;top:8%;left:3%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}@media (max-width: 480px){.hero .logo{margin:0;position:absolute;top:8%;left:12%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}}.hero .content{margin:0;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);z-index:1;width:93vw;text-align:center}.hero .content h1{font-weight:normal;margin-bottom:10px}.hero .content h2{font-size:30px;margin:0}.hero .content h3{font-weight:normal}.hero button{margin-top:15px;z-index:1;background:none;border:1.5px solid white;font-size:15px;font-weight:bold;padding:17px 25px;-webkit-transition:0.3s;transition:0.3s}.hero button:hover{color:#007bc1;background:white}

.services{background:#e5e5e5;text-align:center;color:#342F36;padding:40px 0}.services h1{font-weight:normal}.services h2{font-weight:normal}.services h3{font-weight:normal;max-width:650px;margin:auto}.services .container{padding:20px}.services .tiles{display:flex;flex-wrap:wrap;margin-top:40px;margin-bottom:20px}.services .tiles .tile-container{width:33%}@media (max-width: 960px){.services .tiles .tile-container{width:50%}}@media (max-width: 480px){.services .tiles .tile-container{width:100%}}.services .tiles .tile-container .tile{border:1px solid #B5B5B5;margin:15px;box-shadow:inset 2px 2px 1px 0px #f5f5f5,2px 2px 1px 0px #f5f5f5;text-align:left;padding:5px 20px 0 20px;height:calc(100% - 30px);position:relative}.services .tiles .tile-container .tile .header{display:flex;justify-content:space-between;align-items:center}.services .tiles .tile-container .tile .header .icon{color:#AAACA9;font-size:35px}.services .tiles .tile-container .tile p{margin-top:0}

footer{background:#030324;color:#7983ad;height:100px;position:relative}footer small{text-align:center;margin:0;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

.newsletter{background:rgba(0,122,193,0.854902);max-width:640px;position:fixed;left:0;bottom:0;z-index:1;color:white}@media (max-width: 480px){.newsletter{flex-direction:column}}.newsletter .container{margin:30px;position:relative}@media (max-width: 480px){.newsletter .container{margin:20px}}.newsletter .container h2{margin:0}.newsletter .container .close-icon{cursor:pointer;font-size:12px;margin:0;position:absolute;top:calc(0% - 15px);left:calc(100% + 15px);-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}@media (max-width: 480px){.newsletter .container .close-icon{margin:0;position:absolute;top:0%;left:100%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}}.newsletter .container .sign-up-form{display:flex}@media (max-width: 480px){.newsletter .container .sign-up-form{flex-direction:column}}.newsletter .container .sign-up-form .input-email{padding:3px 0 3px 6px;flex:1 1;font-size:16px;border:none;padding:5px 12px}.newsletter .container .sign-up-form button{font-family:"Ubuntu",sans-serif;background:#F1A701;font-weight:bold;letter-spacing:0.8px;margin-left:10px;font-size:16px;padding:0 15px}@media (max-width: 480px){.newsletter .container .sign-up-form button{margin-left:0;margin-top:15px;height:30px}}::-webkit-input-placeholder{color:#BDBDBD}:-ms-input-placeholder{color:#BDBDBD}::-ms-input-placeholder{color:#BDBDBD}::placeholder{color:#BDBDBD}

